<template>
      <span ref="trigger">
            <slot></slot>
            <div v-show="visible" :transition="transition">
                <div class="modal" :class="modalClass" @click.self="clickMask">
                    <div class="modal-dialog" :class="dialogClass" ref="dialog">
                        <div class="modal-content">
                            <!--Header-->
                            <div class="modal-header" v-if="!noHeader">
                                <slot name="header">
                                    <a type="button" class="close" @click="onCancel">x</a>
                                    <h4 class="modal-title">
                                        <slot name="title">
                                            {{title}}
                                        </slot>
                                    </h4>
                                </slot>
                            </div>
                            <!--Container-->
                            <div class="modal-body">
                                <slot name="content"></slot>
                            </div>
                            <!--Footer-->
                            <div class="modal-footer">
                                <slot name="footer">
                                    <button type="button" v-if="cancel" :class="cancelClass" @click="onCancel">{{cancelText}}</button>
                                    <button type="button" v-if="ok" :class="okClass" @click="onOk">{{okText}}</button>
                                </slot>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="modal-backdrop in"></div>
            </div>
      </span>
</template>

<script>
    export default {
        name : 'c-modal',
        props: {
            show       : Boolean,
            title      : String,
            small      : Boolean,
            large      : Boolean,
            full       : Boolean,
            force      : Boolean,
            cancel     : Boolean,
            ok: Boolean,
            modalClass      : String,
            noHeader   : Boolean,
            transition : {type: String, default: 'modal'},
            okText     : {type: String, default: 'OK'},
            cancelText : {type: String, default: 'Cancel'},
            okClass    : {type: String, default: 'btn blue'},
            cancelClass: {type: String, default: 'btn red btn-outline'},
            closeWhenOK: Boolean
        },

        data () {
            return {
                duration            : null,
                _trigger            : null,
                _keydownEventHandler: (e) => this.onKeydownEvent(e),
                visible             : false
            };
        },
        computed: {
            dialogClass () {
                return {
                    'modal-lg'  : this.large,
                    'modal-sm'  : this.small,
                    'modal-full': this.full
                }
            }
        },
        created () {
            if ( this.show ) {
                document.body.className += ' modal-open';
            }
        },
        mounted() {
            document.addEventListener('keydown', this._keydownEventHandler);
            this._trigger = this.$$(this.$refs.trigger.children[0]);
            this._trigger.on('click', () => this.open());
        },
        beforeDestroy () {
            document.body.className = document.body.className.replace(/\s?modal-open/, '');
            document.removeEventListener('keydown', this._keydownEventHandler);
            this._trigger.off('click')

        },
        watch   : {
            visible (value) {
                // body
                if ( value ) {
                    document.body.className += ' modal-open';
                }
                // modalbody
                else {
                    if ( ! this.duration ) {
                        this.duration = window.getComputedStyle(this.$el)['transition-duration'].replace('s', '') * 1000;
                    }
                    window.setTimeout(() => {
                        document.body.className = document.body.className.replace(/\s?modal-open/, '');
                    }, this.duration || 0);
                }
            }
        },
        methods : {
            open(){
                this.visible = true;
            },
            close(){
                this.visible = false;
            },
            onOk () {
                this.$emit('ok');
                if ( this.closeWhenOK ) {
                    this.close();
                }
            },
            onCancel () {
                this.$emit('cancel');
                this.close();
            },
            onKeydownEvent(e){
                if ( this.show && e.keyCode == 27 ) {
                    this.close();
                }
            },
            // 
            clickMask () {
                if ( ! this.force ) {
                    this.close();
                }
            }
        }
    }
</script>


<style scoped>
    .modal {
        display: block;
    }

    .modal-transition {
        transition: all .6s ease;
    }

    .modal-leave {
        /* transitionEndmodal-leave */
        border-radius: 1px !important;
    }

    .modal-transition .modal-dialog, .modal-transition .modal-backdrop {
        transition: all .5s ease;
    }

    .modal-enter .modal-dialog, .modal-leave .modal-dialog {
        opacity: 0;
        transform: translateY(-30%);
    }

    .modal-enter .modal-backdrop, .modal-leave .modal-backdrop {
        opacity: 0;
    }
</style>


